body{
    font-family: 'Montserrat', sans-serif;
    background: #1D2B64;    
    background: linear-gradient(to left, #F8CDDA, #1D2B64);
    color:white;

}

.UrlSearch{
    font-family: 'Montserrat', sans-serif;
    padding:10px;
    height: 52px;
    left: 35px;
    top: 150px;
    border-radius: 12px;
}

input{
    font-family: 'Montserrat', sans-serif;
    padding-left: 10px;
}

.urlBox{
    position: absolute;
    font-size: 20px;
    width: 45%;
    height: 52px;
    left: 35px;
    top: 150px;
}

.TextArea{
    position: absolute;
    width: 654px;
    height: 413px;
    left: 36px;
    top: 300px;

}

img{
    width: 400px;
    height: auto;
}

.header{
    position: absolute;
    width: 100%;
    height: 100px;
    left: 0px;
    top: 0px;
}

.name{
    font-size: 50px;
    text-transform: capitalize;
}

.Image{
    position: absolute;
    width: 32%;
    height: 400px;
    left: 800px;
    top: 90px;
}

.footer{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
}

.button{
    position: absolute;
    width: 198px;
    height: 40px;
    left: 35px;
    top: 232px;
    background: transparent;
    border-radius: 12px;
    color:White;
    font-size: 20px;
    cursor: pointer;
}

.button:hover{
    background-color: white;
    color:black
}

.AppTitle{
    padding:10px;
    padding-left: 35px;
}